/*MEDIA QUERIES*/
@media (max-width: 645px) {
	.intro-text.intro-text.intro-text {
		font-size: 1.15em;
		font-weight: 400;
	}

	.music-card.music-card.music-card.music-card {
		width: 85%;
		height: 350px;
		padding: 8px;
		padding-top: 25px;
		display: flex;
		flex-direction: column;
		/* use overflow when content needs to be cropped */
		overflow-y: hidden;
		overflow-x: hidden;
		/* Added for centring elems */
		align-items: center;
	}

	.music-card-text.music-card-text.music-card-text.music-card-text {
